import * as React from 'react'
import { NextContext } from 'next'
import Layout from '../components/Layout'
import { ISeo, IPost } from '../interfaces';
import { findOnePost, getImage } from '../utils';
import './post.scss'
import InstagramEmbed from 'react-instagram-embed';
import dynamic from 'next/dynamic';
const ReactHighMark = dynamic(
  () => import('react-mark-highlight'),
  {
    ssr: false
  }
);


type RequestQuery = {
  slug: string,
}

type Props = {
  item: IPost,
  errors?: string
}

class BlogPostPage extends React.Component<Props> {
  static getInitialProps = async ({ query }: NextContext<RequestQuery>) => {

    try {
      const item = await findOnePost(query.slug);
      return { item }
    } catch (err) {
      return { errors: err.message }
    }
  }


  render() {
    const { item, errors } = this.props;

    if (errors) {
      return (
        <Layout>
          <p><span style={{ color: 'red' }}>Error:</span> {errors}</p>
        </Layout>
      )
    }

    const seoMeta: ISeo = {
      title: item.title,
      description: item.summary,
      twitterDescription: item.twitterSummary,
      image: ((item.featuredImage) ? getImage(item.imageName) : 'https://avatars1.githubusercontent.com/u/28115284?s=460&v=4'),
      keywords: item.tags,
      type: 'article',
      url: `sanyamarya.io/post?slug=${item.slug}`,
      article: {
        published_on: item.date,
        section: item.category
      }
    }

    return (
      <Layout seo={seoMeta}>
        <div className="postContainer">
          <div className="header">
            {item.featuredImage &&
              <img src={getImage(item.imageName)}
                alt={item.slug} className="featuredImage" />}

          </div>

          <div className="bodyArea">

            <div className="content">

            
              <ReactHighMark source={item.content} style="diablo" />

              <div className="info">
                <span className="publishedDate">{item.date}</span>
              </div>
            </div>
            <div className="allOther">
              <h1>{item.title}</h1>


              <InstagramEmbed
                url='https://instagr.am/p/3303006698/'
                maxWidth={320}
                hideCaption={false}
                containerTagName='div'
                protocol=''
                injectScript
                onLoading={() => { }}
                onSuccess={() => { }}
                onAfterRender={() => { }}
                onFailure={() => { }}
              />

              {/* <iframe src="//lightwidget.com/widgets/0a88a603ea3459eb9d88612684557926.html" scrolling="no" allowtransparency="true" class="lightwidget-widget" style="width:100%;border:0;overflow:hidden;"></iframe> */}


              {/* <iframe src="//lightwidget.com/widgets/0a88a603ea3459eb9d88612684557926.html" scrolling="no" allowtransparency="true" className="lightwidget-widget" style={{ width: '100%', border: 0, overflow: 'hidden' }}></iframe> */}

              {/* <iframe src="https://snapwidget.com/embed/680088" className="snapwidget-widget" allowTransparency={true} frameBorder="0" scrolling="no" style={{border:"none", overflow:"hidden", width:"100%"}}></iframe> */}
            </div>
          </div>
        </div>

      </Layout>
    )
  }
}

export default BlogPostPage
